/* ------------------------------------*\
    $PAGE SECTIONS
\*------------------------------------ */

.section {
  padding-top: $pad*2;
  padding-bottom: $pad*2;

  @include media('>large') {
    padding-top: $pad*3;
    padding-bottom: $pad*3;
  }
}

.section__hero {
  position: relative;
  padding-top: 0;
  padding-bottom: 0;
  min-height: rem(400);
  margin-left: $space;
  margin-right: $space;

  @include media('>medium') {
    min-height: rem(500);
  }

  @include media('>large') {
    min-height: rem(600);
    background-attachment: fixed;
  }

  @include media('>xlarge') {
    min-height: rem(800);
  }

  &-caption {
    position: absolute;
    bottom: rem(-25);
    left: 0;
    right: 0;
    max-width: rem(750);
    width: 100%;
  }

  &-content {
    position: absolute;
    flex: 0 0 auto;
    max-width: rem(600);
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
  }
}



